@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap);
.header {
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  text-align: center;
}
.todosContainer {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin: 0px 5vw;
}

.textContainer {
  width: 100%;
}

.todosText {
  word-wrap: break-word;   
  overflow-wrap: break-word;
  word-break: break-word;
  color: rgb(121, 120, 120);
}

.btnContainer {
  display: flex;
  align-items: center;
}

.btnComplete {
  font-size: 16px;
  margin: 0px 5px;
  height: 36px;
  width: 36px;
  background-color: rgb(5, 206, 95);
  color: rgb(235, 222, 222);
  border: 0px;
  border-radius: 5px;
  transition: transform 0.2s linear;
}

.btnComplete:hover {
  cursor: pointer;
  transform: scale(1.1);
  color: rgb(5, 206, 95);
  background-color: rgb(235, 222, 222);
}

.btnDelete {
  font-size: 16px;
  height: 36px;
  width: 36px;
  background-color: rgb(204, 23, 23);
  color: rgb(235, 223, 223);
  border: 0px;
  border-radius: 5px;
  transition: transform 0.2s linear;
}

.btnDelete:hover {
  cursor: pointer;
  transform: scale(1.1);
  color: rgb(204, 23, 23);
  background-color: rgb(235, 223, 223);
}
.addTodoForm {
  display: flex;
  flex-direction: row;
  margin: 0px 5vw;
  margin-bottom: 24px;
}

.inputText {
  flex-grow: 9;
  color: rgb(151, 150, 150);
  background-color: #f3f4f5;
  padding: 10px 10px;
  border-radius: 5px;
  margin-right: 5px;
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
}

input::-webkit-input-placeholder {
  color: gray;
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
}

input:-ms-input-placeholder {
  color: gray;
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
}

input::-ms-input-placeholder {
  color: gray;
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
}

input::placeholder {
  color: gray;
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
}

.submitBtn {
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  background-color:#319ef0;
  color: rgb(236, 230, 230);
  border: 0px;
  border-radius: 5px;
  width: 77px;
  transition: transform 0.2s linear;
}

.submitBtn:hover {
  cursor: pointer;
  transform: scale(1.1);
  color: #319ef0;
  background-color: rgb(236, 230, 230);
}
body {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-size: 1rem; 
  line-height: 1.4;
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
}

.App {
  padding: 10px 0px 50px 0px;
  margin: 5px 5px;
  border-radius: 5px;
  box-shadow: 0 4px 6px 0 hsla(0, 0%, 0%, 0.2);
  border-top: 6px solid rgb(192, 94, 192);
}

@media only screen and (min-width: 767px) {
  .App {
    max-width: 720px;
    margin: 5px auto;
  }
}
